.not-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;

    &-title {
        font-size: 3rem;
        font-weight: 700;
        margin-bottom: 1rem;
    }

    &-subtitle {
        font-size: 1.5rem;
        font-weight: 400;
        margin-bottom: 1rem;
    }

    &-button {
        font-size: 1rem;
        font-weight: 400;
        padding: 0.5rem 1rem;
        border: 1px solid #000;
        border-radius: 0.25rem;
        background-color: #fff;
        color: #000;
        text-decoration: none;
        transition: all 0.2s ease-in-out;

        &:hover {
            background-color: #000;
            color: #fff;
        }
    }
}