.start-screen {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    pointer-events: none;

    &-top-layer {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 10vh;
        background-color: rgb(0, 0, 0, 0.5);
        display: flex;
        justify-content: space-between;
        align-items: center;
        opacity: 0;
        transition: opacity 0.2s ease-in-out;

        &.show {
            opacity: 1;
        }

        .info {
            color: white;
            display: flex;
            justify-content: space-between;
            height: 100%;
            width: 50%;
            padding: 1rem;

            &-account {
                width: 50%;
                display: flex;
                gap: 10px;
                font-size: 1.25rem;
                padding-top: 10px;
                padding-left: 10px;

                &-avatar {
                    height: 90%;
                    aspect-ratio: 1;
                    object-fit: cover;
                }
            }

            &-time {
                width: 50%;
                font-size: 1.25rem;
                padding-top: 10px;
                padding-left: 10px;
                gap: 5px;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;
            }
        }

        .player {
            display: flex;
            justify-content: space-between;
            height: 100%;
            width: 50%;
            padding: 1rem;
        }
    }


    &-logo {
        position: relative;
        width: 600px;
        height: 600px;

        &-container {
            @keyframes slideLeft {
                0% { left: 0px; }
                100% { left: -200px; }
            }

            @keyframes slideLeftReset {
                0% { left: -200px; }
                100% { left: 0px; }
            }

            position: absolute;
            top: 0;
            left: 0;
            width: 600px;
            height: 600px;
        }

        &-ripple {
            @keyframes ripple {
                0% {
                    transform: scale(1);
                    opacity: 0.5;
                }
                75% {
                    transform: scale(1.5);
                    opacity: 0;
                }
                100% {
                    transform: scale(1);
                    opacity: 0;
                }
                
            }

            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: 3px solid #fff;
            border-radius: 50%;
            opacity: 0.5;
            animation: ripple 1s infinite;
        }

        &-static {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            pointer-events: all;
        }

        &-pulse {
            @keyframes pulse {
                0% { transform: scale(0.9); }
                95% { transform: scale(1); }
                100% { transform: scale(0.9); }
            }

            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0.5;
            animation: pulse 1s infinite;
            pointer-events: none;
        }
    }

    &-menu {
        &-container {
            position: absolute;
            top: 0;
            left: 0;
            width: 0;
            height: 100%;
            pointer-events: none;
            border-radius: 20% / 50%;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;

            @keyframes slideRight {
                0% {
                    width: 0px;
                    left: 0px;
                }
                25% {
                    width: 0px;
                }
                100% { 
                    width: 100%;
                    left: 200px; 
                }
            }

            @keyframes slideRightReset {
                0% {
                    width: 100%;
                    left: 200px;
                }
                25% {
                    width: 0px;
                }
                100% { 
                    width: 0;
                    left: 0px; 
                }
            }
        }

        &-button {
            height: 20%;
            width: 100%;
            background: linear-gradient(to right, #8967ef, #523b95);
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            transition: margin-left 0.2s ease-in-out;
            pointer-events: all;

            &:hover {
                margin-left: 40px;
            }

            .text {
                margin-left: 50px;
                width: 25%;
                font-size: 3rem;
                color: white;
            }
        }
    }
}